import React, {useState, useEffect} from 'react' 
import './Chat.css'
import a from './aa.jpg'
import {Avatar, IconButton} from '@material-ui/core';
import {AttachFile, MoreVert, SearchOutlined} from '@material-ui/icons';
import MicIcon from '@material-ui/icons/Mic';
import InsertEmoticonIcon from '@material-ui/icons/InsertEmoticon';
import { useParams} from 'react-router-dom';
import db from './firebase';
import firebase from 'firebase';
import {useStateValue} from "./StateProvider";
import moment from 'moment';
import {storage, ts} from './firebase'
import PermMediaIcon from '@material-ui/icons/PermMedia';

const Chat = () => {
    const [{user}, dispatch] = useStateValue();

    const {roomId} =useParams();
    const [roomName, setRoomName]=useState("")
    const [messages, setMessages]=useState([])
    const [media, setMedia]=useState(' ')
    const [url, setUrl] = useState("");
    const [error, setError] = useState("");
    const [time,setTime]=useState("");
    const [progress, setProgress] = useState(0);

    useEffect(() => {

        db.collection('rooms').doc(roomId).onSnapshot(snapshot => {
            setRoomName(snapshot.data().name);
        });

        db.collection('rooms').doc(roomId)
        .collection('messages').orderBy('timestamp', 'asc')
        .onSnapshot(snapshot=>{
            setMessages(snapshot.docs.map(doc=>doc.data()))
        })

  
    }, [roomId])

    

    const filehandler = async (e)=>{

        if(e.target.files[0]){
            await setMedia(e.target.files[0])
            alert('Click Send')
        }
        else{
            setMedia(null);
            setError('Please select an image file');
          }
          
    }


    const upload =()=>{
        storageRef.put(media).on('state_changed', (snap) => {
            let percentage = (snap.bytesTransferred / snap.totalBytes) * 100;
            setProgress(percentage);
          }, (err) => {
            setError(err);
          }, 
        async () => {
        let urL = await storageRef.getDownloadURL();   
        const createdAt = ts(); 
          setUrl(urL);
          alert(url);
          setTime(createdAt);  
          
          db.collection('rooms').doc(roomId).collection('messages').add({
            timestamp: firebase.firestore.FieldValue.serverTimestamp(),
            image: url,
            name: user?.displayName,
        })
       
      }          
    );
    }

    useEffect(() => {

        storageRef.put(media).on('state_changed', (snap) => {
            let percentage = (snap.bytesTransferred / snap.totalBytes) * 100;
            setProgress(percentage);
          }, (err) => {
            setError(err);
          }, 
        async () => {
        let urL = await storageRef.getDownloadURL();   
        const createdAt = ts(); 
          setUrl(urL);
          alert(url);
          setTime(createdAt);  
          
          db.collection('rooms').doc(roomId).collection('messages').add({
            timestamp: firebase.firestore.FieldValue.serverTimestamp(),
            image: url,
            name: user?.displayName,
        })
         setMedia(" ")
      }          
    );
  
      }, [media])
  

    const storageRef = storage.ref(`images/${media.name}`);
    console.log(url)


    const  [input,setInput]=useState("")

    const send=(e)=>{
        e.preventDefault();
            db.collection('rooms').doc(roomId).collection('messages').add({
                message: input,
                name: user?.displayName,
                timestamp: firebase.firestore.FieldValue.serverTimestamp(),
                
            })
        setInput(" ")
    }
    console.log(input);
    return (
        <div className='chat'> 
             <div className='chat-header'>
                <Avatar src={a}/>
                <div className='chat-header-info'>
                    <h3>{roomName}</h3>

                    
                    <p>Last Active .{" "}
                      
                      {moment( messages[messages.length - 1]?.
                     timestamp?.toDate()).startOf('minute').fromNow()}
                      
                    </p>
                </div>
                    <div className='chat-header-right'>
                    <IconButton>
                        <SearchOutlined/>
                    </IconButton>
                    <IconButton>
                        <label htmlFor='image'>
                        <AttachFile style={{cursor:'pointer'}}/>         
                        <input type='file' onChange={filehandler} id='image' style={{display:'none'}} />
                        </label>
                    </IconButton>
                    <IconButton>
                        <MoreVert/>
                    </IconButton>
                    </div>
                
             </div>
             
             
             <div className='chat-body'>

                 {messages.map(message=>(
                      <p className={`message ${message.name===user.displayName &&'reciever'}`}>
                           {message.message} <img className='media' src= {message.image}/>
                      <span className='sender-name'> {message.name}</span>
                      <span className='timestamp'>          
                           {new Date(
                             message.timestamp?.toDate())
                            .toUTCString()}      
                        </span>
                      </p>
                 ))}

                
                 
             </div>
                     
             <div className='chat-footer'>
             <InsertEmoticonIcon />
             <img src={url} style={{width:'20px', height:'20px'}}/>
             <form onSubmit={(e)=>e.preventDefault}>
                <input value={input} type="text" placeholder="Type a message"
                 onChange={(e)=>setInput(e.target.value)}/>

                <button type="submit" onClick={send} > Send a Message</button>          
                </form>
      
                <button type='submit' onClick={upload}> Send Image </button> 
              
                <MicIcon/>
             </div>
           
             {console.log(media.name)}
        </div>
    )
}

export default Chat
